import { useEffect, useState } from "react";
import styles from "./SettingPage.module.scss";
import { AuthInput } from "components/AuthInput";
import { getProfile } from "api/userinfo";
const id = localStorage.getItem("id");

export const SettingPage = () => {
  const [user, setUser] = useState([]);
  useEffect(() => {
    const data = async () => setUser(await getProfile(id));
    setUser(data);
  }, []);

  return (
    <>
      <div className={styles.mainbarContainer}>
        <header className={styles.settingPageHeader}>
          <h1 className={styles.settingPageTitle}></h1>
        </header>

        <div className={styles.inputContainer}>
          <AuthInput type="text" label="" value={user.account} />
        </div>
        <div className={styles.inputContainer}>
          <AuthInput type="text" label="" value={user.name} />
        </div>
        <div className={styles.inputContainer}>
          <AuthInput type="text" label="Email" value={user.email} />
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="password"
            label=""
            value=""
            placeholder=""
          />
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="password"
            label=""
            value=""
            placeholder=""
          />
        </div>
        <button className={styles.btn}></button>
      </div>
    </>
  );
};
